import React from "react";
import { connect } from "react-redux";
import { enToFa, priceToPersian, digitToComma } from "../utils/numberUtils";
import {
  setNewBill,
  deleteItem,
  getCustomerByPhoneNumber,
  updateBill,
  updateBillItem
} from "../../actions/SaleActions";
import {
  Button,
  Modal,
  Divider,
  Header,
  Segment,
  Form,
  Card,
  Popup,
  Icon,
  Message,
  Label
} from "semantic-ui-react";
import { getProductsByCode } from "../../actions/DepositoryActions";
import NewBillPopup from "./newBillPopup";
import { toastr } from "react-redux-toastr";

const INITIAL_STATE = {
  sumProductTotalPrice: 0,
  data: {},
  isOpenAddItem: false,
  formValidation: {},
  phone_number: "",
  used_points: "",
  branch: 1,
  discount: 0,
  editting: "",
  branchOptions: [{ key: "1", value: "1", flag: "ir", text: " " }],
  isEnableEdit: {
    discount: false,
    used_points: false
  },
  customerData: {},
  editedData: {},
  notFound: null,
  productData: {}
};

class ShowBillInformation extends React.Component {
  state = INITIAL_STATE;

  componentDidMount() {
    this.syncStateWithProps();
  }

  syncStateWithProps() {
    if (!this.state.data.items) {
      this.setState(
        { data: this.props.data, discount: this.props.data.discount },
        () => {
          if (this.state.data.buyer) {
            this.getCustomerData(this.state.data.buyer.phone_number);
            this.sumProductTotalPrice();
            this.getProductData();
          }
        }
      );
    }
  }

  getProductData = () => {
    this.props.getProductsByCode(this.state.editedData.code).then(() => {
      this.setState({
        notFound: false,
        productData: this.props.productsList
      });
    });
  };

  toggleAddItemPopup = () => {
    this.setState(prevState => ({ isOpenAddItem: !prevState.isOpenAddItem }));
  };

  deleteItem = index => {
    var r = window.confirm("      ");
    if (r === true) {
      this.props
        .deleteItem(this.state.data.items[index].pk)
        .then(({ data }) => {
          this.setState({ data }, () => {
            this.sumProductTotalPrice();
          });
          toastr.success("     ");
        })
        .catch(() => {
          toastr.error("    ");
        });
    }
  };

  editItem = index => {
    this.setState({
      editting: index,
      editMode: true
    });
  };

  submitChanges = pk => {
    let preparedData = {
      name: this.state.editedData.name,
      code: this.state.editedData.code,
      selling_price: this.state.editedData.selling_price,
      amount: this.state.editedData.amount,
      discount: this.state.editedData.discount,
      end_of_roll_amount: this.state.editedData.end_of_roll_amount,
      end_of_roll: this.state.editedData.end_of_roll
    };
    this.props.updateBillItem(pk, preparedData).then(() => {
      this.setState({
        editting: "",
        editMode: false
      });
      this.props.refetch();
      console.log("sent name", this.state.editedData.name);
      console.log("sent code", this.state.editedData.code);
    });
  };

  cancelChanges = () => {
    this.setState({
      name: null,
      code: null,
      selling_price: null,
      amount: null,
      discount: null,
      end_of_roll_amount: null,
      end_of_roll: null,
      editMode: false,
      editting: null
    });
  };

  inputChange = (event, inputName) => {
    this.setState({
      [inputName]: event.target.value
    });
    if (inputName === "phone_number") {
      this.getCustomerData(event.target.value);
    }
  };

  getCustomerData = phone_number => {
    if (phone_number.length === 11) {
      this.props.getCustomerByPhoneNumber(phone_number).then(({ data }) => {
        this.setState({
          customerData: data
        });
      });
    }
  };

  edit = inputName => {
    let prepareData = {
      discount: Number(this.state.discount),
      used_points: Number(this.state.used_points)
    };
    this.props.updateBill(this.state.data.pk, prepareData).then(() => {
      this.setState({
        discount: Number(this.state.discount),
        used_points: Number(this.state.used_points)
      });
      this.props.refetch();
    });
    this.setState({
      isEnableEdit: {
        [inputName]: true
      }
    });
  };

  applyEdit = inputName => {
    this.setState({
      isEnableEdit: {
        ...this.state.isEnableEdit,
        [inputName]: false
      }
    });
    if (inputName === "used_points") {
      if (this.state.used_points > this.state.customerData.points) {
        alert("         ");
        this.setState({
          data: { ...this.state.data, used_points: this.props.data.used_points }
        });
      } else {
        this.props
          .updateBill(this.state.data.pk, {
            used_points: Number(this.state.used_points)
          })
          .then(() => {
            this.setState({ used_points: Number(this.state.used_points) });
            this.props.refetch();
          });
      }
    } else if (inputName === "discount") {
      this.props
        .updateBill(this.state.data.pk, {
          discount: Number(this.state.discount)
        })
        .then(() => {
          this.setState({ discount: Number(this.state.discount) });
          this.props.refetch();
        });
    }
  };

  submitItemPopup = data => {
    this.setState(
      {
        itemsDataSheet: [...this.state.itemsDataSheet, data],
        formValidation: { ...this.state.formValidation, items: false }
      },
      () => {
        this.sumProductTotalPrice();
      }
    );
    this.toggleAddItemPopup();
  };

  refetchModalData = response => {
    this.setState({ ...this.state, data: response.data }, () => {
      this.sumProductTotalPrice();
    });
  };

  sumProductTotalPrice = () => {
    let preSumArray = [];
    let sum = 0;
    if (this.state.data.items)
      preSumArray = this.state.data.items.map(item => {
        return Number(item.final_price);
      });
    preSumArray.forEach(item => {
      sum += item;
    });
    this.setState({ sumProductTotalPrice: sum });
  };

  handleChange = (e, status) => {
    if (status === "code") {
      this.handleSearchChange(this.state.editedData.code);
    }
    this.setState({
      editedData: {
        ...this.state.editedData,
        [status]: e.target.value
      }
    });
  };

  handleSearchChange = value => {
    this.setState({ product: Number(value) }, () => {
      if (
        String(this.state.editedData.code).length < 1 ||
        String(this.state.editedData.code) === "0"
      ) {
        this.setState({ notFound: NaN });
      } else {
        this.props
          .getProductsByCode(this.state.editedData.code)
          .then(() => {
            this.setState({
              notFound: false,
              productData: this.props.productsList
            });
          })
          .catch(() => {
            this.setState({
              notFound: true,
              productData: {}
            });
          });
      }
    });
  };

  handleCodeInputClick = e => {
    this.setState(
      {
        editedData: {
          code: e.target.value
        }
      },
      () => {
        if (Number(this.state.editedData.code)) {
          this.props.getProductsByCode(this.state.editedData.code).then(() => {
            this.setState({
              notFound: false,
              productData: this.props.productsList
            });
          });
        }
      }
    );
  };

  itemsRender = (item, index) => {
    return (
      <Card.Group key={index} id="s-showInfromationModel">
        <Card fluid>
          <Card.Content>
            <Card.Header className="yekan">
              {item.product.name}
              {!this.state.editMode ? (
                <Button
                  icon
                  color="red"
                  onClick={() => this.deleteItem(index)}
                  className="pointer"
                  labelPosition="right"
                  size="mini"
                  style={{ marginRight: "10px" }}
                >
                  <span> </span>
                  <Icon name="trash" />
                </Button>
              ) : null}
              {this.state.editMode ? null : (
                <Button
                  icon
                  color="teal"
                  onClick={() => this.editItem(index)}
                  className="pointer"
                  labelPosition="right"
                  size="mini"
                  style={{ marginRight: "10px" }}
                >
                  <span></span>
                  <Icon name="edit" />
                </Button>
              )}
              {!this.state.editMode ? (
                <Message
                  compact
                  size="mini"
                  color="teal"
                  className="yekan form-message"
                >
                           .
                </Message>
              ) : null}
            </Card.Header>
          </Card.Content>
          <Card.Content>
            <Form>
              <Form.Group widths="equal">
                <Form.Input
                  className="rtl text-right placeholder-rtl"
                  readOnly={this.state.editting !== index ? true : false}
                  fluid
                  defaultValue={item.product.name}
                  onChange={e => this.handleChange(e, "name")}
                  label=" "
                />
                <Popup
                  content={
                    <React.Fragment>
                      {this.state.notFound === false ? (
                        <Label color="teal" className="rtl text-center">
                          <p>
                            <span> :</span>&nbsp;
                            <span>{enToFa(this.state.productData.name)}</span>
                          </p>
                          <p>
                            <span>  :</span>&nbsp;
                            <span>
                              {enToFa(this.state.productData.stock_amount)}
                            </span>
                            &nbsp;
                            <span></span>
                          </p>
                          <p>
                            <span>  :</span>&nbsp;
                            <span>
                              {enToFa(
                                priceToPersian(
                                  this.state.productData.selling_price
                                )
                              )}
                            </span>
                            <span>&nbsp; </span>
                          </p>
                        </Label>
                      ) : (
                        <Label color="red">
                          <Icon name="warning circle" />
                          <span>    </span>
                        </Label>
                      )}
                    </React.Fragment>
                  }
                  position="bottom center"
                  on="focus"
                  trigger={
                    <Form.Input
                      className="ltr placeholder-rtl"
                      readOnly={this.state.editting !== index ? true : false}
                      fluid
                      defaultValue={item.product.code}
                      onChange={e => this.handleChange(e, "code")}
                      onClick={e => this.handleCodeInputClick(e)}
                      label=" "
                    />
                  }
                />
                <Form.Input
                  className="ltr placeholder-rtl"
                  readOnly
                  fluid
                  defaultValue={priceToPersian(item.product.selling_price)}
                  onChange={e => this.handleChange(e, "selling_price")}
                  label=" "
                />
                <Form.Input
                  className="ltr placeholder-rtl"
                  readOnly={this.state.editting !== index ? true : false}
                  fluid
                  defaultValue={item.amount}
                  onChange={e => this.handleChange(e, "amount")}
                  label={`()`}
                />
                <Form.Input
                  className="ltr placeholder-rtl"
                  readOnly={this.state.editting !== index ? true : false}
                  fluid
                  defaultValue={item.discount}
                  onChange={e => this.handleChange(e, "discount")}
                  label=""
                />
              </Form.Group>
              <Form.Group>
                <Form.Input
                  className={`ltr placeholder-rtl ${
                    item.end_of_roll ? "" : "hidden"
                  }`}
                  readOnly={this.state.editting !== index ? true : false}
                  defaultValue={item.end_of_roll_amount}
                  onChange={e => this.handleChange(e, "end_of_roll_amount")}
                  label="  "
                />
                <Form.Checkbox
                  toggle
                  className="ltr placeholder-rtl"
                  readOnly={this.state.editting !== index ? true : false}
                  checked={item.end_of_roll}
                  onChange={e => this.handleChange(e, "end_of_roll")}
                  label=" "
                />
              </Form.Group>
              {this.state.editting !== index ? null : (
                <React.Fragment>
                  <Button
                    color="green"
                    onClick={() => this.submitChanges(item.pk)}
                  >
                    <span></span>
                  </Button>
                  <Button color="gray" onClick={this.cancelChanges}>
                    <span></span>
                  </Button>
                </React.Fragment>
              )}
            </Form>
          </Card.Content>
        </Card>
      </Card.Group>
    );
  };

  labelRender = (labelName, inputName) => {
    if (this.state.isEnableEdit[inputName]) {
      return (
        <span
          className="d-flex"
          style={{
            marginBottom: "2.5px",
            alignItems: "center",
            justifyContent: "flex-end"
          }}
        >
          <Label
            onClick={() => this.applyEdit(inputName)}
            className="pointer"
            style={{ marginRight: 10 }}
            size="mini"
            color="green"
          >
            <Icon name="checkmark" /> 
          </Label>
          <span>{labelName}</span>
        </span>
      );
    } else {
      return (
        <span
          className="d-flex"
          style={{
            marginBottom: "2.5px",
            alignItems: "center",
            justifyContent: "flex-end"
          }}
        >
          <Label
            onClick={() => this.edit(inputName)}
            className="pointer"
            style={{ marginRight: 10 }}
            size="mini"
            color="teal"
          >
            <Icon name="edit" /> 
          </Label>
          <span>{labelName}</span>
        </span>
      );
    }
  };

  render() {
    return Object.keys(this.state.data).length > 0 ? (
      <Modal
        id="add-bill"
        closeOnDimmerClick={false}
        dimmer="blurring"
        className="text-right rtl yekan"
        open={this.props.open}
        onClose={this.props.onClose}
      >
        <Modal.Header className="yekan"></Modal.Header>
        <Modal.Content scrolling>
          <Modal.Description>
            <Label color="blue">
              <span> :&nbsp;</span>
              <span>{enToFa(String(this.state.customerData.points))}</span>
              <span>&nbsp;</span>
            </Label>
            <Form>
              <Form.Group unstackable widths={2}>
                <Form.Input
                  className="ltr placeholder-rtl"
                  readOnly
                  defaultValue={this.state.data.buyer.phone_number}
                  label="  "
                  type="number"
                  onChange={e => this.inputChange(e, "phone_number")}
                  placeholder="  "
                />
                <Form.Input
                  className="ltr placeholder-rtl"
                  readOnly={!this.state.isEnableEdit.used_points}
                  error={!this.state.isEnableEdit.used_points}
                  defaultValue={this.state.data.used_points}
                  label={() =>
                    this.labelRender("  ", "used_points")
                  }
                  type="number"
                  onChange={e => this.inputChange(e, "used_points")}
                  placeholder="  "
                />
              </Form.Group>
              <Form.Group widths={2}>
                <Form.Dropdown
                  className="ltr placeholder-rtl text-right"
                  readOnly
                  defaultValue={"1"}
                  placeholder=""
                  selection
                  label={""}
                  options={this.state.branchOptions}
                />
                <Form.Input
                  className="ltr placeholder-rtl"
                  readOnly={!this.state.isEnableEdit.discount}
                  error={!this.state.isEnableEdit.discount}
                  defaultValue={this.state.data.discount}
                  label={() => this.labelRender(" ", "discount")}
                  type="number"
                  onChange={e => this.inputChange(e, "discount")}
                  placeholder=" "
                />
              </Form.Group>
              <Form.Group widths={2}>
                <Form.Input className="invisible" hidden={true} />
                <Form.Input
                  className="rtl placeholder-rtl text-right"
                  readOnly={true}
                  label="  "
                  value={`${digitToComma(
                    Math.round(
                      this.state.sumProductTotalPrice - this.state.discount
                    )
                  )} `}
                  type="text"
                />
              </Form.Group>
              {this.state.data.items &&
              this.state.data.items.length > 0 ? null : (
                <Message
                  icon="inbox"
                  color="red"
                  header="      "
                  content={
                    <span>
                                
                             {" "}
                      <b>  </b>  
                    </span>
                  }
                />
              )}
              <div className="text-center">
                <Popup
                  style={{ top: -35 }}
                  content={
                    <NewBillPopup
                      onClose={this.toggleAddItemPopup}
                      phoneNumber={this.state.data.buyer.phone_number}
                      refetch={this.refetchModalData}
                      pk={this.state.data.pk}
                      onSubmit={this.submitItemPopup}
                    />
                  }
                  open={this.state.isOpenAddItem}
                  className="no-filter"
                  position="bottom center"
                  wide="very"
                  trigger={
                    <Button
                      circular
                      onClick={() => {
                        this.toggleAddItemPopup(this.state.isOpenAddItem);
                      }}
                      color="green"
                      size="huge"
                      icon="add"
                    />
                  }
                />
              </div>
              <Segment
                hidden={
                  this.state.data.items && this.state.data.items.length <= 0
                }
              >
                <Header as="h3" floated="right">
                  <span> </span>{" "}
                  <Label className="norm-latin">
                    <span className="yekan">  :&nbsp;</span>
                    <span>
                      {digitToComma(
                        Math.round(this.state.sumProductTotalPrice)
                      )}
                    </span>
                    <span className="yekan">&nbsp;</span>
                  </Label>
                </Header>

                <Divider clearing />
                {this.state.data.items &&
                  this.state.data.items.map((item, index) => {
                    return this.itemsRender(item, index);
                  })}
              </Segment>
            </Form>
          </Modal.Description>
        </Modal.Content>

        <Modal.Actions>
          <Button
            color="black"
            onClick={() => {
              this.props.onClose();
              this.setState(INITIAL_STATE);
            }}
            disabled={this.state.editMode ? true : false}
          >
            <span></span>
          </Button>
        </Modal.Actions>
      </Modal>
    ) : (
      <React.Fragment></React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    productsList: state.depository.productsList
  };
};

export default connect(mapStateToProps, {
  setNewBill,
  deleteItem,
  getCustomerByPhoneNumber,
  updateBill,
  updateBillItem,
  getProductsByCode
})(ShowBillInformation);
